.api-call{
    &__first{
        border: 1px solid #f3f3f3;
        &_list{
            .ant-list-item{
                 padding:0.5rem;
            }
        }
    }
    &__method{
        color: #f6b03b;
    }
  
    &__middle{
      padding: 1rem 1.5rem;
      &__para{
        background: #f2f4f5;
        border:1px solid #f2f4f5;
      
      }
      &__body{
        padding:1rem !important;
        background: #f2f4f5;
        overflow: hidden;
        text-overflow: ellipsis;
        margin-top:1rem;
      }
    }
    &__first,&__middle,&__last{
        max-height: 30rem;
        overflow-y: scroll;
    }
    &__last{
       
        h3{
            background-color: #fff;
            padding-left: 1rem;
         
        }
        p{
            padding: 1rem;
            background:#f2f4f5;
            margin: 1rem 1.5rem;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
    &__para{
     border: none;
     margin: 0;
     padding: 0;
     font-size: 13px;
     color: #314659;
     overflow: auto;
     line-height: 1.7;
     background: #fff;
    //  border: 1px solid #f3f3f3;
    }
    h3{
        // font-size:1.8rem;
        color: #0d1a26;
        font-variant: tabular-nums;
        font-weight: 500;
        clear: both;
    }
    &__folderstructure{
        padding:1rem;
    }
    &__dropdown{
        cursor: pointer;
     
    }
    &__iconheader{
        border: 1px solid #ddd;
        background: #f3f3f3;
        h3{
            margin-left: 1rem;
            margin-top: 0.7rem;
        }
    }
    &__icon{
        font-size: 1rem;
        padding: 1rem;
    }
    &__beautifier{
        color:#1890ff;
    }
    &__requestContent{
        transition:all 0.3s ease-in;
        border: 1px solid #dddddd;
        padding: 0.5rem;
    }
      
    .ant-tree.ant-tree-directory > li.ant-tree-treenode-selected > span.ant-tree-node-content-wrapper:before, .ant-tree.ant-tree-directory .ant-tree-child-tree > li.ant-tree-treenode-selected > span.ant-tree-node-content-wrapper:before {
        background: rgba(24, 144, 255,0.8);
    }

    @media all and (max-width:768px){
        .api-call{
            &__icon{
                padding: 0.5rem;
            }
             &__middle{
                 padding-left: 0;
                 padding-right: 0;
             }
        }
    }
   
}